/* animations circles */
.signal {
  position: absolute;
  height: 10vh;
  width: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 3;
}
.pulse {
  position: relative;
  font-size: 20px;
  background: rgb(46, 196, 223);
  height: 20px;
  width: 20px;
  border-radius: 50%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  animation-name: innercircle;
  animation-duration: 3s;
  animation-iteration-count: infinite;
}
.pulse::after {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  background: rgb(46, 196, 223);
  opacity: 0.6;
  z-index: -1;
  height: 20px;
  width: 20px;
  transform: scale(1.5);
  border-radius: 50%;
  animation: circle 3s linear infinite;
}
.pulse::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  background: rgb(46, 196, 223);
  z-index: -1;
  height: 20px;
  width: 20px;
  transform: scale(1.5);
  border-radius: 50%;
  animation: outercircle;
  animation-duration: 3s;
  animation-iteration-count: infinite;
}
@keyframes circle {
  0% {
    opacity: 0.6;
    transform: scale(1);
  }
  50% {
    opacity: 0.6;
    transform: scale(1.4);
  }
  75% {
    opacity: 0;
    transform: scale(1.4);
  }
  100% {
    opacity: 0;
    transform: scale(1.4);
  }
}

@keyframes innercircle {
  from {
    transform: scale(1);
  }
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.1);
  }
  75% {
    transfrom: scale(1.1);
  }
  to {
    transform: scale(1);
  }
}
@keyframes outercircle {
  from {
    opacity: 0.6;
    transform: scale(1);
  }
  0% {
    transform: scale(1);
  }
  25% {
    opacity: 0.6;
    transform: scale(1.6);
  }
  50% {
    opacity: 0.3;
    transform: scale(1.8);
  }
  75% {
    opacity: 0;
    transfrom: scale(2.4);
  }
  to {
    opacity: 0;
    transform: scale(2.4);
  }
}
