  width: 100%;
  height: 100px;
  border-radius: 7px;
  transition: 0.5s;
  position: absolute;
  left: 0%;
  top: calc(50% - 3px);
}

.rotating-cards-1 > div.current {
  transform: translate3d(-0.5%, 0%, 50px);
  /*black*/
  color: #212529;
  /*color: #2ec4df;*/
}
