  transform: translate3d(-5%, 70%, -50px);
  /*back */
  color: #21252955;
  /*color: #2ec4df55;*/
}

.rotating-cards > div.current {
  transform: translate3d(2%, -45%, -50px);
  color: #2ec4df;
}
.rotating-cards > div.prev {
  transform: translate3d(-2%, -110%, -150px);
  color: #2ec4df55;
}
.rotating-cards > div.next {
