.Modal-Popup {
  position: fixed;
  top: 0;
  z-index: 15;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(32, 32, 32, 0.5);
}

.Modal-exit {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 15px;
  right: 15px;
  height: 25px;
  width: 25px;
  font-size: 15px;
  color: #8f8f8f;
  border: 0;
  outline: 0;
  cursor: pointer;
  transition: 0.3s ease;
}

.Modal-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 65vh;
  max-width: 75vw;
}
.Modal-nav {
  height: 200px;
}

.Modal-nav-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.Modal-nav-buttons li {
  display: inline;
  padding: 15px 15px;
  margin: 5px 10px;
  text-align: center;
  border-radius: 10px;
  color: rgb(30, 30, 30);
  cursor: pointer;
  background-color: #b5dada;
  text-decoration: none;
  transition: 0.3s ease;
}

.Modal-nav-buttons li:hover {
  background-color: #c6eeee;
}

.Modal-Content {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
  /* padding: 5% 0 0 0; */
  height: 65vh;
  width: 100%;
  background: #ffffff;
  border-radius: 10px;
}

.Modal-img {
  width: 75%;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: contain;
  height: 100%;
  margin: 0 25px;
}

.Modal-Content .Modal-Content-text {
  display: flex;
  margin: 50px 25px;
  flex-direction: column;
  font-size: 0.8em;
}

.Modal-Content-text p {
  font-family: "Arial";
  font-size: 1.2em;
  margin-top: 15%;
  align-self: center;
}
